'use client';

import { usePathname } from "next/navigation";



export default function SellerAuthLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();

  const steps = [
    { label: "Identity verification", key: "identity" },
    { label: "Store setup", key: "store" },
    { label: "Account setup", key: "account" },
    { label: "Verify", key: "verify" },
  ];

  const currentIndex = steps.findIndex((step) =>
    pathname.includes(step.key)
  );


  return (
    <div className="min-h-screen grid lg:grid-cols-[40%_60%] bg-white w-full">
      {/* LEFT SIDE */}
      <div className="bg-[#DDFFF4] px-20 py-25 flex flex-col justify-between">
        <div>
          <h1 className="text-3xl font-semibold text-black">
            Setup your account
          </h1>

          <p className="mt-2 text-sm text-gray-700">
            Letâ€™s get you up and running immediately.
            <br />
            Kindly proceed to verify and setup your store
          </p>

          <div className="mt-12 space-y-10">
            {steps.map((step, index) => {
              let status: "current" | "completed" | "upcoming" = "upcoming";

              if (index < currentIndex) status = "completed";
              if (index === currentIndex) status = "current";

              return (
                <Step
                  key={step.key}
                  label={step.label}
                  status={status}
                />
              );
            })}
          </div>

        </div>

        <div className="bg-[#003625] text-white p-6 rounded-[20px]">
          <p className="text-sm">
            Verified sellers make 10x more sales than unverified sellers.
            <br />
            You can boost a product to get even better visibility and more sales.
          </p>
        </div>
      </div>

      {/* RIGHT SIDE */}
      <div className="bg-[#F4F4F4] flex items-center justify-center p-20">
        {children}
      </div>
    </div>

  );
}

function Step({
  label,
  status,
}: {
  label: string;
  status: "current" | "completed" | "upcoming";
}) {
  return (
    <div className="flex items-center gap-6">
      {/* Circle */}
      <div
        className={`w-6 h-6 rounded-full flex items-center justify-center
        ${status === "current"
            ? "border-[1px] border-[#0D3B2E] bg-[#CFEFE6]"
            : status === "completed"
              ? "border-[1px] border-[#0D3B2E] bg-[#CFEFE6]"
              : "border-[1px] border-[#D9D9D9]"
          }`}
      >
        <div
          className={`w-4 h-4 rounded-full ${status === "current"
            ? "bg-[#CFCFCF]"
            : status === "completed"
              ? "bg-[#0D3B2E]"
              : "bg-[#CFCFCF]"
            }`}
        />
      </div>

      {/* Label */}
      <span
        className={`text-xl ${status === "current"
          ? "text-black font-medium"
          : status === "completed"
            ? "text-black font-medium"
            : "text-black"
          }`}
      >
        {label}
      </span>
    </div>
  );
}
